export default {
  data () {
    return {
      list: []
    }
  },

  onLoad () {
    this.$http.getGiftRankList({
      shop_id: this.$mp.query.shopid
    }).then(res => {
      let className = 'rank4'
      this.list = res.data.map((item, index) => {
        if (index === 0) {
          className = 'rank1'
        } else if (index === 1) {
          className = 'rank2'
        } else if (index === 2) {
          className = 'rank3'
        } else {
          className = 'rank4'
        }
        item.className = className

        return item
      })
      console.log(JSON.stringify(res.data))
    })
  },

  methods: {
    
  },
}
